import {anotherCounterAdd, anotherCounterSub} from '../actions/actionTypes';

const initialState = {
  anotherCounter: 0,
};

export default function anotherCounter(state = initialState, action) {
  switch(action.type) {
    case anotherCounterAdd:
      return ({
        anotherCounter: state.anotherCounter + action.value,
      });
    case anotherCounterSub:
      return ({
        anotherCounter: state.anotherCounter - action.value,
      });
    default:
      return state;
  }
};
