import React from 'react';
import {connect} from 'react-redux';
import { ButtonGroup, Button, Col, Row } from 'reactstrap';

import {onAnotherCounterAdd, onAnotherCounterSub} from "../../redux/actions/actions";

class AnotherCounter extends React.Component {
  constructor(props) {
    super(props);
    this.addButtonProps = {
      color: 'success',
      size: 'lg',
      onClick: () => props.onAnotherCounterAdd(5),
    };
    this.subButtonProps = {
      color: 'danger',
      size: 'lg',
      onClick: () => props.onAnotherCounterSub(5),
    };
  };
  render() {
    const {anotherCounter} = this.props.anotherCounter;
    return (
        <Col>
          <Row>
            <h1>Ещё один счётчик {anotherCounter}</h1>
          </Row>
          <Row>
            <ButtonGroup>
              <Col style={{padding: 0}}>
                <Button {...this.addButtonProps}>Увеличить</Button>
              </Col>
              <Col>
                <Button {...this.subButtonProps}>Уменьшить</Button>
              </Col>
            </ButtonGroup>
          </Row>
        </Col>
    )
  }
}

const mapStateToProps = (state) => ({
  anotherCounter: state.anotherCounter,
});
const mapDispatchToProps = (dispatch) => ({
  onAnotherCounterAdd: (number) => dispatch(onAnotherCounterAdd(number)),
  onAnotherCounterSub: (number) => dispatch(onAnotherCounterSub(number)),
});

export default connect(mapStateToProps, mapDispatchToProps)(AnotherCounter)
