import {counterAdd, counterSub, anotherCounterAdd, anotherCounterSub} from './actionTypes';

export const onCounterAdd = () => ({type: counterAdd});
export const onCounterSub = () => ({type: counterSub});
export const onAnotherCounterAdd = (number) => ({type: anotherCounterAdd, value: number});
export const onAnotherCounterSub = (number) => ({type: anotherCounterSub, value: number});
