import React from 'react';
import {connect} from 'react-redux';
import { ButtonGroup, Button, Col, Row } from 'reactstrap';

import {onCounterAdd, onCounterSub} from "../../redux/actions/actions";

class Counter extends React.Component {
  constructor(props) {
    super(props);
    this.addButtonProps = {
      color: 'success',
      size: 'lg',
      onClick: props.onCounterAdd,
    };
    this.subButtonProps = {
      color: 'danger',
      size: 'lg',
      onClick: props.onCounterSub,
    };
  };
  render() {
    const {counter} = this.props.counter;
    return (
        <Col>
          <Row>
            <h1>Счётчик {counter}</h1>
          </Row>
          <Row>
            <ButtonGroup>
              <Col style={{padding: 0}}>
                <Button {...this.addButtonProps}>Увеличить</Button>
              </Col>
              <Col>
                <Button {...this.subButtonProps}>Уменьшить</Button>
              </Col>
            </ButtonGroup>
          </Row>
        </Col>
    )
  }
}

const mapStateToProps = (state) => ({
  counter: state.counter,
});
const mapDispatchToProps = (dispatch) => ({
  onCounterAdd: () => dispatch(onCounterAdd()),
  onCounterSub: () => dispatch(onCounterSub()),
});

export default connect(mapStateToProps, mapDispatchToProps)(Counter)
