import {counterAdd, counterSub} from '../actions/actionTypes';

const initialState = {
  counter: 0,
};

export default function counter(state = initialState, action) {
  switch(action.type) {
    case counterAdd:
      return ({
        counter: state.counter + 1,
      });
    case counterSub:
      return ({
        counter: state.counter - 1,
      });
    default:
      return state;
  }
};
